@media only screen and ( min-width : 1025px ) {

  header {
    background-image: url('https://images.unsplash.com/photo-1550859492-d5da9d8e45f3?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80');
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 1620px;
    height: 250px;
    width: 100%;
  }

  .tittleHeader {
    visibility: hidden;
  }

  .geralHeader {
    align-items: center;
    background: radial-gradient(circle, black
    0%, rgba(172, 170, 168, 0.932) 320%);
    display: flex;
    flex-direction: column;
    height: 980px;
    width: 300px;
  }

  .imagemHeader {
    height: 130px;
    margin-top: 40px;
  }

  .loadingHeader {
    color: white;
    font-size: 30px;
    font-weight: 900;
    margin-top: 60px;
    text-align: center;
  }

  .nomeFotoHeader {
    align-items: center;
    border-top: 1px solid rgb(71, 70, 70);
    display: flex;
    justify-content: center;
    margin-top: 280px;
    padding: 20px 10px 0;
  }

  .divProgressH {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: -10px;
  }

  .progressloadingH {
    animation: progressH 2s infinite steps(6);
    background: linear-gradient(#63615f 0 0) left/0% 100% no-repeat #e9e9e9;
    height: 25px;
    margin: 0 auto;
    mask: radial-gradient(circle closest-side, #fa9a00 94%, #0000) left/20% 100%;
    width: 180px;
  }

  .progressH {
    color: #e4e4e4;

    /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
    font-family: Gotham-Black;
    font-size: 24px;
    font-variant-caps: all-petite-caps;
    font-weight: 900;
    margin: 0 auto;
    margin-bottom: 10px;
    margin-top: 10px;
  }

  @keyframes progressH {

    100% {
      background-size: 120% 100%;
    }

  }

  .pHeader {
    color: white;

    /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
    font-family: Gotham-Black;
    font-size: 28px;
    font-variant-caps: all-petite-caps;
    font-weight: 500;
    margin-bottom: 5px;
    max-height: 100px;
    max-width: 180px;
    text-align: center;
  }

  .fotoPerfilHeader {
    border: 2px solid rgb(27, 26, 26);
    border-radius: 50%;
    height: 60px;
    margin-right: 10px;
  }

  .navHeader {
    align-items: start;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
  }

  .linkHeader {
    color: rgb(223, 211, 211);

    /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
    font-family: Gotham-Black;
    font-size: 24px;
    margin: 15px 20px;
    padding: 20px 40px;
  }

  .linkHeader:hover {
    text-shadow: 0 2px 5px rgba(112, 55, 1, 0.322);
    transform: scale(1.05);
  }

  .searchHeader {
    color: rgb(107, 100, 100);
    margin: 0 20px 0 0;
    transform: scale(1.5);
  }

  .heartHeader {
    color: rgb(107, 100, 100);
    margin: 0 20px 0 0;
    transform: scale(1.3);
  }

  .profileHeader {
    color: rgb(107, 100, 100);
    margin: 0 20px 0 0;
    transform: scale(1.3);
  }

}
